import React from "react"
import { Navbar, NavbarTitle, NavbarIconLink } from "styled/Navbar"
import { Body } from "styled/Body"
import { Text, Subtitle } from "styled/Typography"
import { ContactPanel, ContactDetails, Img } from "./styled"
import axios from "axios"

class Contacts extends React.Component {
  constructor() {
    super()
    this.state = {
      contacts: [],
      loading: true
    }
  }
  componentDidMount() {
    axios
      .get("http://private-36f1e-contactstest.apiary-mock.com/contacts")
      .then(result => {
        this.setState({
          loading: false,
          contacts: result.data.items
        })
      })
  }
  render() {
    return (
      <div>
        <Navbar>
          <NavbarTitle>Contacts</NavbarTitle>
          <NavbarIconLink to="/addcontact">+</NavbarIconLink>
        </Navbar>
        <Body>
          {this.state.loading ? (
            "loading"
          ) : (
            <>
              {this.state.contacts.map((x, i) => (
                <ContactPanel key={x.id} to={`/orders/${x.id}`}>
                  <Img src={x.pictureUrl || "https://via.placeholder.com/50"} />
                  <ContactDetails>
                    <Subtitle>{x.name}</Subtitle>
                    <Text>{x.phone}</Text>
                  </ContactDetails>
                </ContactPanel>
              ))}
            </>
          )}
        </Body>
      </div>
    )
  }
}

export default Contacts
